<template>
  <div class="main">
    <el-tree
        :data="treeData"
        highlight-current
        :expand-on-click-node="false"
        @node-click="handleNodeClick"
    >
      <span slot-scope="{ node, data }">
        <span>{{ node.label }}</span>
        <span>{{ node.isLeaf && data.articles ? ('(' + data.articles.length + ')') : ''}}</span>
      </span>
    </el-tree>
  </div>
</template>

<script>
  export default {
    name: 'Classify',
    data() {
      return {
        treeData: [],
        articles: [],
        tags: []
      }
    },
    mounted() {
      // 加载文章分类树
      this.loadClassifies()
    },
    methods: {
      /**
       * 加载文章分类树
       */
      loadClassifies() {
        this.$ajax.get('/config/config.json')
          .then(res => {
            this.treeData = res.data
          });
      },
      /**
       * 树节点点击回调
       * @param data
       * @param node
       */
      handleNodeClick(data, node) {
        const app = this;
        if (node.isLeaf) {
          app.$bus.$emit('refreshArticles', data.articles, node.data.label)
        }
      }
    }
  }
</script>

<style scoped>
  .main {
    padding-left: 20px;
  }
</style>
