<template>
  <div class="main">
    <div class="article" v-for="(article, index) in articles" :key="index">
      <p><a class="title" :href="`/#/article?${calculateTargetUrlParams(article)}`" target="_blank">{{index + 1}}.&nbsp;&nbsp;&nbsp;{{article.name}}</a></p>
      <p><Icon type="md-calendar" />&nbsp;&nbsp;&nbsp;{{article.date}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Articles",
    data() {
      return {
        articles: [],
        tag: ''
      }
    },
    created() {
      const app = this;
      app.$bus.$on('refreshArticles', (data, tag) => {
        app.articles = data;
        app.tag = tag;
      })
    },
    methods: {
      calculateTargetUrlParams(article) {
        const file = encodeURI(article.url);
        const tag = encodeURI(this.tag + ' > ' + article.name);
        return `file=${file}&tag=${tag}`
      }
    }
  }
</script>

<style scoped>
  .main {
    padding-left: 20px;
    padding-right: 20px;
  }

  .article {
    border-bottom: darkgray 1px dashed;
    padding: 20px 0;
  }

  .title {
    font-size: 20px;
    font-weight: bold;
    color: dimgrey;
  }

  .title:hover {
    color: #409EFF;
  }
</style>
