<template>
  <div class="main">
    <div class="title">
      {{tag}}
    </div>
    <div class="content">
      <iframe :src="fileUrl" style="border: hidden;height: 100%; width: 100%"></iframe>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Article",
    components: {},
    data() {
      return {
        html: null
      }
    },
    computed: {
      fileUrl: function () {
        return this.$route.query.file
      },
      tag: function () {
        const tag = this.$route.query.tag
        document.title = tag.substring(tag.lastIndexOf(' > ') + 3);
        return tag
      }
    }
  }
</script>

<style scoped>
  .main {
    width: 100%;
    height: 100%;
    padding: 20px 0 20px 20px;
    overflow: no-display;
  }

  .title {
    font-weight: bold;
    font-size: 16px;
    color: #000;
    padding: 5px;
  }

  .content {
    height: 100%;
  }
</style>
